<template>
	<div id="map_main">
		<div id="menu_wrap" class="bg_white">
			<div class="option">
				<div>
					<form>
						<input
							type="text"
							id="keyword"
							size="15"
							placeholder=" "
						/>
						<button @click.prevent="searchFunc"></button>
					</form>
				</div>
			</div>
			<hr />
			<ul id="placesList"></ul>
			<div id="pagination"></div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'KakaoMap',
	data() {
		return {
			map: null,
			infowindow: null,
			markers: [],
		};
	},
	mounted() {
		this.initMap();
	},
	methods: {
		//   function
		initMap() {
			let container = document.getElementById('map_main');
			let options = {
				center: new kakao.maps.LatLng(37.53343, 126.981279),
				level: 8,
			};
			this.map = new kakao.maps.Map(container, options);
		},
		searchFunc() {
			var temp = this;
			//    
			var ps = new kakao.maps.services.Places();
			//          
			var infowindow = new kakao.maps.InfoWindow({
				zIndex: 1,
				removable: true,
			});
			//   
			searchPlaces();
			//    
			function searchPlaces() {
				var keyword = document.getElementById('keyword').value;
				if (!keyword.replace(/^\s+|\s+$/g, '')) {
					alert(' !');
					return false;
				}
				//      
				ps.keywordSearch(keyword, placesSearchCB);
			}
			//      
			function placesSearchCB(data, status, pagination) {
				if (status === kakao.maps.services.Status.OK) {
					//       
					displayPlaces(data);
					//   
					displayPagination(pagination);
				} else if (status === kakao.maps.services.Status.ZERO_RESULT) {
					alert('   .');
					return;
				} else if (status === kakao.maps.services.Status.ERROR) {
					alert('    .');
					return;
				}
			}
			//      
			function displayPlaces(places) {
				var listEl = document.getElementById('placesList'),
					menuEl = document.getElementById('menu_wrap'),
					fragment = document.createDocumentFragment(),
					bounds = new kakao.maps.LatLngBounds();
				// listStr = '';
				//      
				removeAllChildNods(listEl);
				//     
				removeMarker();
				for (var i = 0; i < places.length; i++) {
					//    
					var placePosition = new kakao.maps.LatLng(places[i].y, places[i].x),
						marker = addMarker(placePosition, i),
						itemEl = getListItem(i, places[i]); //    Element 
					//       
					// LatLngBounds   
					bounds.extend(placePosition);

					//    mouseover 
					//     
					// mouseout    
					(function (marker, places) {
						kakao.maps.event.addListener(marker, 'click', function () {
							displayInfowindow(marker, places);
						});
						itemEl.onmouseover = function () {
							displayInfowindow(marker, places);
						};
						itemEl.onmouseout = function () {
							infowindow.close();
						};
					})(marker, places[i]);
					fragment.appendChild(itemEl);
				}
				//     Elemnet 
				listEl.appendChild(fragment);
				menuEl.scrollTop = 0;
				//       
				temp.map.setBounds(bounds);
			}
			//   Element  
			function getListItem(index, places) {
				var el = document.createElement('li'),
					itemStr =
						'<span style="float:left;position:absolute;width:36px; height:37px;margin:10px 0 0 10px;background:url(https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/marker_number_blue.png) no-repeat;background-position: 0 -' +
						(10 + index * 46) +
						'px;}" class="markerbg marker_' +
						(index + 1) +
						'"></span>' +
						'<div style=""border-bottom:1px solid rgba(53, 53, 53, 0.993);overflow: hidden;min-height: 65px;>' +
						'<div class="info" style="padding:10px 0 10px 55px;text-overflow: ellipsis;overflow: hidden;white-space: nowrap;cursor:pointer;">' +
						'<h5 style="text-overflow: ellipsis;overflow: hidden;white-space: nowrap;padding-bottom: 2px;">' +
						places.place_name +
						'</h5>';
				if (places.road_address_name) {
					itemStr +=
						'    <span style="display:blockpadding-bottom: 2px;">' +
						places.road_address_name +
						'</span>';
				} else {
					itemStr += '    <span>' + places.address_name + '</span>';
				}
				itemStr += '</div></div>';

				el.innerHTML = itemStr;
				el.className = 'item';
				return el;
			}
			//       
			function addMarker(position, idx) {
				var imageSrc =
						'https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/marker_number_blue.png', //   url,   
					imageSize = new kakao.maps.Size(36, 37), //   
					imgOptions = {
						spriteSize: new kakao.maps.Size(36, 691), //   
						spriteOrigin: new kakao.maps.Point(0, idx * 46 + 10), //       
						offset: new kakao.maps.Point(13, 37), //      
					},
					markerImage = new kakao.maps.MarkerImage(
						imageSrc,
						imageSize,
						imgOptions,
					),
					marker = new kakao.maps.Marker({
						position: position, //  
						image: markerImage,
						clickable: true,
					});
				marker.setMap(temp.map); //    
				temp.markers.push(marker); //    
				return marker;
			}
			//       
			function removeMarker() {
				for (var i = 0; i < temp.markers.length; i++) {
					temp.markers[i].setMap(null);
				}
				temp.markers = [];
			}

			//      
			function displayPagination(pagination) {
				var paginationEl = document.getElementById('pagination'),
					fragment = document.createDocumentFragment(),
					i;
				//    
				while (paginationEl.hasChildNodes()) {
					paginationEl.removeChild(paginationEl.lastChild);
				}
				for (i = 1; i <= pagination.last; i++) {
					var el = document.createElement('a');
					el.href = '#';
					el.innerHTML = i;
					el.style = 'display:inline-block;margin-right:10px;';

					if (i === pagination.current) {
						el.className = 'on';
					} else {
						el.onclick = (function (i) {
							return function () {
								pagination.gotoPage(i);
							};
						})(i);
					}
					fragment.appendChild(el);
				}
				paginationEl.appendChild(fragment);
			}
			//        
			//   
			function displayInfowindow(marker, places) {
				// : link/map/ID  /link/map/,
				// : '/link/to/,,'  '/link/to/ID'
				var content =
					'<div style="padding:5px;z-index:1;">' +
					places.place_name +
					'</div><br><a href="https://place.map.kakao.com/' +
					places.id +
					'" style="color:#8a8a8a" target="_blank"></a> <a href="https://map.kakao.com/link/to/' +
					places.id +
					'" style="color:#8a8a8a" target="_blank"></a>';
				infowindow.setContent(content);
				infowindow.open(temp.map, marker);
			}
			//    Element  
			function removeAllChildNods(el) {
				while (el.hasChildNodes()) {
					el.removeChild(el.lastChild);
				}
			}
		},
	},
};
</script>

<style scoped>
#map_main a,
#map_main a:hover,
#map_main a:active {
	color: #000;
	text-decoration: none;
}
#map_main * {
	font-family: 'Malgun Gothic', dotum, '', sans-serif;
	font-size: 12px;
}
#map_main {
	height: 0;
	padding-bottom: 100%;
	position: relative;
	overflow: hidden;
	margin-bottom: 1rem;
	border-radius: 0.5em;
	box-shadow: 0.5em -0.3em 10px 1px rgba(143, 143, 143, 0.2);
	z-index: 1;
}
.button-group {
	margin: 10px 0px;
}
button {
	margin: 0 3px;
}
#menu_wrap {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	width: 25%;
	margin: 10px 0 30px 10px;
	padding: 5px;
	overflow-y: auto;
	background: rgba(255, 255, 255, 0.7);
	border-bottom: #5f5f5f;
	z-index: 2;
	font-size: 12px;
	border-radius: 10px;
}
.bg_white {
	background: #fff;
}
#menu_wrap hr {
	display: block;
	height: 1px;
	border: 0;
	border-top: 2px solid #5f5f5f;
	margin: 3px 0;
}
#menu_wrap .option {
	text-align: center;
}
#menu_wrap .option p {
	margin: 10px 0;
}
#menu_wrap .option button {
	margin-left: 5px;
}
#pagination {
	margin: 10px auto;
	text-align: center;
}
#pagination a {
	display: inline-block;
	margin-right: 10px;
}
#pagination .on {
	font-weight: bold;
	cursor: default;
	color: #777;
}
</style>
